/* Footer */

#footer {
	padding: 5em 0 2em 0;
	background-color: #f6f6f6;
}

#footer > .inner {
	display: -moz-flex;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-moz-flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	-moz-flex-direction: row;
	-webkit-flex-direction: row;
	-ms-flex-direction: row;
	flex-direction: row;
}

#footer > .inner > * > :last-child {
	margin-bottom: 0;
}

#footer > .inner section:nth-child(1) {
	width: calc(66% - 2.5em);
	margin-right: 2.5em;
}

#footer > .inner section:nth-child(2) {
	width: calc(33% - 2.5em);
	margin-left: 2.5em;
}

#footer > .inner .copyright {
	width: 100%;
	padding: 0;
	margin-top: 1em;
	list-style: none;
	font-size: 0.8em;
	color: rgba(88, 88, 88, 0.5);
}

#footer > .inner .copyright a {
	color: inherit;
}

#footer > .inner .copyright li {
	display: inline-block;
	border-left: solid 1px rgba(88, 88, 88, 0.15);
	line-height: 1;
	padding: 0 0 0 1em;
	margin: 0 0 0 1em;
}

#footer > .inner .copyright li:first-child {
	border-left: 0;
	padding-left: 0;
	margin-left: 0;
}

@media screen and (max-width: 1280px) {
	#footer {
		padding: 5em 0 3em 0;
	}

	#footer > .inner section:nth-child(1) {
		width: calc(66% - 1.25em);
		margin-right: 1.25em;
	}

	#footer > .inner section:nth-child(2) {
		width: calc(33% - 1.25em);
		margin-left: 1.25em;
	}
}

@media screen and (max-width: 980px) {
	#footer > .inner section:nth-child(1) {
		width: 66%;
		margin-right: 0;
	}

	#footer > .inner section:nth-child(2) {
		width: calc(33% - 2.5em);
		margin-left: 2.5em;
	}
}

@media screen and (max-width: 736px) {
	#footer {
		padding: 3em 0 1em 0;
	}

	#footer > .inner {
		-moz-flex-direction: column;
		-webkit-flex-direction: column;
		-ms-flex-direction: column;
		flex-direction: column;
	}

	#footer > .inner section:nth-child(1) {
		width: 100%;
		margin-right: 0;
		margin: 3em 0 0 0;
	}

	#footer > .inner section:nth-child(2) {
		-moz-order: -1;
		-webkit-order: -1;
		-ms-order: -1;
		order: -1;
		width: 100%;
		margin-left: 0;
	}

	#footer > .inner .copyright {
		margin-top: 3em;
	}
}

@media screen and (max-width: 480px) {
	#footer > .inner .copyright {
		margin-top: 3em;
	}

	#footer > .inner .copyright li {
		border-left: 0;
		padding-left: 0;
		margin: 0.75em 0 0 0;
		display: block;
		line-height: inherit;
	}

	#footer > .inner .copyright li:first-child {
		margin-top: 0;
	}
}

/* Form */

form {
	margin: 0 0 2em 0;
	overflow-x: hidden;
}

form > :last-child {
	margin-bottom: 0;
}

form > .fields {
	display: -moz-flex;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-moz-flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	width: calc(100% + 3em);
	margin: -1.5em 0 2em -1.5em;
}

form > .fields > .field {
	-moz-flex-grow: 0;
	-webkit-flex-grow: 0;
	-ms-flex-grow: 0;
	flex-grow: 0;
	-moz-flex-shrink: 0;
	-webkit-flex-shrink: 0;
	-ms-flex-shrink: 0;
	flex-shrink: 0;
	padding: 1.5em 0 0 1.5em;
	width: calc(100% - 1.5em);
}

form > .fields > .field.half {
	width: calc(50% - 0.75em);
}

form > .fields > .field.third {
	width: calc(100% / 3 - 0.5em);
}

form > .fields > .field.quarter {
	width: calc(25% - 0.375em);
}

@media screen and (max-width: 480px) {
	form > .fields {
		width: calc(100% + 3em);
		margin: -1.5em 0 2em -1.5em;
	}

	form > .fields > .field {
		padding: 1.5em 0 0 1.5em;
		width: calc(100% - 1.5em);
	}

	form > .fields > .field.half {
		width: calc(100% - 1.5em);
	}

	form > .fields > .field.third {
		width: calc(100% - 1.5em);
	}

	form > .fields > .field.quarter {
		width: calc(100% - 1.5em);
	}
}

label {
	display: block;
	font-size: 0.9em;
	font-weight: 900;
	margin: 0 0 1em 0;
}

input[type='text'],
input[type='password'],
input[type='email'],
input[type='tel'],
select,
textarea {
	-moz-appearance: none;
	-webkit-appearance: none;
	-ms-appearance: none;
	appearance: none;
	background-color: transparent;
	border: none;
	border-radius: 0;
	border-bottom: solid 1px #c9c9c9;
	color: inherit;
	display: block;
	outline: 0;
	padding: 0;
	text-decoration: none;
	width: 100%;
}

input[type='text']:invalid,
input[type='password']:invalid,
input[type='email']:invalid,
input[type='tel']:invalid,
select:invalid,
textarea:invalid {
	box-shadow: none;
}

input[type='text']:focus,
input[type='password']:focus,
input[type='email']:focus,
input[type='tel']:focus,
select:focus,
textarea:focus {
	border-bottom-color: #f2849e;
	box-shadow: inset 0 -1px 0 0 #f2849e;
}

select {
	background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='40' height='40' preserveAspectRatio='none' viewBox='0 0 40 40'%3E%3Cpath d='M9.4,12.3l10.4,10.4l10.4-10.4c0.2-0.2,0.5-0.4,0.9-0.4c0.3,0,0.6,0.1,0.9,0.4l3.3,3.3c0.2,0.2,0.4,0.5,0.4,0.9 c0,0.4-0.1,0.6-0.4,0.9L20.7,31.9c-0.2,0.2-0.5,0.4-0.9,0.4c-0.3,0-0.6-0.1-0.9-0.4L4.3,17.3c-0.2-0.2-0.4-0.5-0.4-0.9 c0-0.4,0.1-0.6,0.4-0.9l3.3-3.3c0.2-0.2,0.5-0.4,0.9-0.4S9.1,12.1,9.4,12.3z' fill='%23c9c9c9' /%3E%3C/svg%3E");
	background-size: 1.25rem;
	background-repeat: no-repeat;
	background-position: calc(100% - 1rem) center;
	height: 3em;
	padding-right: 3em;
	text-overflow: ellipsis;
}

select option {
	background: #ffffff;
}

select:focus::-ms-value {
	background-color: transparent;
}

select::-ms-expand {
	display: none;
}

input[type='text'],
input[type='password'],
input[type='email'],
select {
	height: 3em;
}

textarea {
	padding: 0;
	min-height: 3.75em;
}

input[type='checkbox'],
input[type='radio'] {
	-moz-appearance: none;
	-webkit-appearance: none;
	-ms-appearance: none;
	appearance: none;
	display: block;
	float: left;
	margin-right: -2em;
	opacity: 0;
	width: 1em;
	z-index: -1;
}

input[type='checkbox'] + label,
input[type='radio'] + label {
	text-decoration: none;
	color: #585858;
	cursor: pointer;
	display: inline-block;
	font-size: 1em;
	font-weight: 300;
	padding-left: 2.55em;
	padding-right: 0.75em;
	position: relative;
}

input[type='checkbox'] + label:before,
input[type='radio'] + label:before {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	font-family: FontAwesome;
	font-style: normal;
	font-weight: normal;
	text-transform: none !important;
}

input[type='checkbox'] + label:before,
input[type='radio'] + label:before {
	border-radius: 4px;
	border: solid 1px #c9c9c9;
	content: '';
	display: inline-block;
	height: 1.8em;
	left: 0;
	line-height: 1.725em;
	position: absolute;
	text-align: center;
	top: 0;
	width: 1.8em;
}

input[type='checkbox']:checked + label:before,
input[type='radio']:checked + label:before {
	background: #585858;
	border-color: #585858;
	color: #ffffff;
	content: '\f00c';
}

input[type='checkbox']:focus + label:before,
input[type='radio']:focus + label:before {
	border-color: #f2849e;
	box-shadow: 0 0 0 1px #f2849e;
}

input[type='checkbox'] + label:before {
	border-radius: 4px;
}

input[type='radio'] + label:before {
	border-radius: 100%;
}

#footer .inner {
	width: 100%;
	max-width: 68em;
	margin: 0 auto;
}

@media screen and (max-width: 736px) {
	#footer .inner {
		padding: 0 1.25em;
	}
}

